                  <a-descriptions-item label=""></a-descriptions-item>
                  <a-descriptions-item label="">4</a-descriptions-item>
                  <a-descriptions-item label="">10000</a-descriptions-item>
                  <a-descriptions-item label="">-</a-descriptions-item>
                  <a-descriptions-item label="">:</a-descriptions-item> -->
